import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { PaymentInstallmentMonths } from "@enum/app_enum";

@Component({
  selector: 'app-installment-month-list',
  templateUrl: './installment-month-list.component.html',
  styleUrls: ['./installment-month-list.component.css']
})
export class InstallmentMonthListComponent implements OnInit {

  public monthsList = [ PaymentInstallmentMonths.One, PaymentInstallmentMonths.Three, PaymentInstallmentMonths.Six ]
  //public selectedMonth = PaymentInstallmentMonths.One
  @Input() months 
  @Output() selectInstallmentMonths: any = new EventEmitter<any>()

  constructor() { }

  ngOnInit() {}
  
  select(mon){
    //this.selectedMonth = month
    this.selectInstallmentMonths.emit(mon)
  }

}
