import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { ReactiveFormsModule, FormsModule } from "@angular/forms";

// com lists
import { UserListsComponent } from './user-lists/user-lists.component';
import { UserCreateComponent } from './user-create/user-create.component';
import { StudentCreateComponent } from './student-create/student-create.component';
import { StudentViewComponent } from "../../shared/basic-components/student-view/student-view.component";
import { InstallmentMonthListComponent } from "./installment-month-list/installment-month-list.component";
// other internal module
import { BasicComponentsModule } from "@basic-components/basic-components.module";
import { AuthGuardService } from '@app/core/guards/auth-guard.service';
import { Role } from '@app/enum/app_enum';


const routes: Routes = [
  {
    path: 'admin-management/:role',
    component: UserListsComponent,
    canActivate: [AuthGuardService],
    data: {
      roles: [Role.Admin],
    },
  },
  {
    path: 'teacher-management/:role',
    component: UserListsComponent,
    canActivate: [AuthGuardService],
    data: {
      roles: [Role.Admin],
    },

  },
  {
    path: 'parent-management/:role',
    component: UserListsComponent,
    canActivate: [AuthGuardService],
    data: {
      roles: [Role.Admin],
    },

  },
  {
    path: 'student-management/:role',
    component: UserListsComponent,
    canActivate: [AuthGuardService],
    data: {
      roles: [Role.Admin],
    },

  },
  {
    path: 'studentAffair-management/:role',
    component: UserListsComponent,
    canActivate: [AuthGuardService],
    data: {
      roles: [Role.Admin],
    },

  },
  {
    path: 'create/:role',
    component: UserCreateComponent,
    canActivate: [AuthGuardService],
    data: {
      roles: [Role.Admin],
    },
  },
  {
    path: 'admin-management/edit/:role/:userId',
    component: UserCreateComponent,
    canActivate: [AuthGuardService],
    data: {
      roles: [Role.Admin],
    },
  },
  {
    path: 'teacher-management/edit/:role/:userId',
    component: UserCreateComponent,
    canActivate: [AuthGuardService],
    data: {
      roles: [Role.Admin],
    },
  },
  {
    path: 'parent-management/edit/:role/:userId',
    component: UserCreateComponent,
    canActivate: [AuthGuardService],
    data: {
      roles: [Role.Admin],
    },
  },
  {
    path: 'student-management/edit/:role/:studentId',
    component: UserCreateComponent,
    canActivate: [AuthGuardService],
    data: {
      roles: [Role.Admin],
    },
  },
  {
    path: 'studentAffair-management/edit/:role/:userId',
    component: UserCreateComponent,
    canActivate: [AuthGuardService],
    data: {
      roles: [Role.Admin],
    },
  },
];

const comLists = [
  UserListsComponent,
  UserCreateComponent,
  StudentCreateComponent,
  StudentViewComponent,
  InstallmentMonthListComponent
]

@NgModule({
  declarations: [comLists],
  imports: [
    CommonModule,
    BasicComponentsModule,
    RouterModule.forChild(routes),
    ReactiveFormsModule,
    FormsModule
  ],
  exports: [
    comLists,
  ]
})
export class UsersModule { }
