import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

// internal 
import { UserService } from "@core/services/user.service";
import { AuthenticationService } from "@core/authentication/authentication.service";
import { Label, ActionLabel } from '@app/enum/app_enum';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-user-lists',
  templateUrl: './user-lists.component.html',
  styleUrls: ['./user-lists.component.css']
})
export class UserListsComponent implements OnInit,OnDestroy {
  public currentUserRole: String;
  public searchData: String;
  public selectedRouteRole: String;
  public routeDirLists: any = {
    routeName: [],
    routeLink: []
  };

  public tableHeader = [Label.createDate, Label.name, Label.email, Label.phonenumber, Label.action];
  public actionBtns = [ActionLabel.edit, ActionLabel.delete];
  public objectLabel = ["createdDate", "username", "email", "phoneNumber"];
  public filter: any = {};
  public actionLinks: any = {
    editLink: "",
    deleteLink: "users",
    idKeyname: "userId",
  }
  public limit: any = 10;
  public page: any = 1;
  public isAsc: boolean = true;
  public userLists: any = {};
  public totalCount: any;
  public userId: any;
  public subscription: Subscription;

  constructor(private _userService: UserService,
    private _auth: AuthenticationService,
    private _activeRoute: ActivatedRoute,) { }

  ngOnInit(): void {
    this.currentUserRole = this._auth.currentUserValue.role.toLowerCase();

    // get URL parameters
    this._activeRoute.params.subscribe(params => {
      this.filter = {
        sortColumn: this.objectLabel[0],
        sortDirection: "asc",
        index: 0
      };
      this.userLists = null;

      // this.routeDirLists = {};
      this.selectedRouteRole = params.role.charAt(0).toUpperCase() + params.role.slice(1); // --> Name must match wanted parameter
      this.routeDirLists.routeName.push(this.selectedRouteRole);

      this.actionLinks.editLink = this.currentUserRole + "/users/" + this.selectedRouteRole.toLowerCase() + "-management/edit/" + this.selectedRouteRole.toLocaleLowerCase();
      this.getUserLists();
    });


  }

  getUserLists() {
    this.subscription = this._userService.userLists(this.searchData, this.page, this.limit, this.filter.sortColumn, this.filter.sortDirection, this.selectedRouteRole)
      .subscribe(
        data => {
          let temp: any = {};
          temp.data = data.users;
          temp.tableHeader = this.tableHeader;
          temp.actionBtns = this.actionBtns;
          temp.objectLabel = this.objectLabel;
          temp.btnLink = this.actionLinks;
          this.userLists = temp;
          this.searchData = null;
          this.totalCount = data.totalCount;
        });
  }

  search(val: String) {

    this.searchData = val;
    setTimeout(() => {
      this.getUserLists();
    }, 200);

  }

  pageChanges(page: any) {
    this.page = page;
    setTimeout(() => {
      this.getUserLists();
    }, 200);
  }

  onFilter(e: any) {
    this.filter = e;
    this.getUserLists();
  }

  ngOnDestroy(){
    this.subscription.unsubscribe();
  }
}
