declare var $: any;
@Component({
  selector: 'app-user-create',
  templateUrl: './user-create.component.html',
  styleUrls: ['./user-create.component.css']
})
export class UserCreateComponent implements OnInit, OnDestroy {
  public isCrop: boolean = false;
  blankCrop: boolean = false;
  imgDemoSlider: boolean = false;
  validProfile: boolean = false;
  public input: any;
  public uploadCrop: any;
  public uploadImg: any;
  public userForm: FormGroup
  public submitted = false
  public userRole = [Role.Admin, Role.Parent, Role.Teacher, Role.StudentAffair]
  public position = [];
  public relationship = [Relationship.Mother, Relationship.Father,Relationship.Guardian]
  public routeDirLists: any = {
    routeName: [AdminSideMenu.User, CommonExpands.Create],
    routeLink: ["users/admin-management/admin"]
  }
  public isStudentCreate: boolean = false
  public studentList: any = []
  public userId: any;
  public isParent: boolean = false;
  public currentRole: any;
  public isChangePassword: boolean;
  public routeRole: any;
  public subscription: Subscription;


  constructor(private formBuilder: FormBuilder, private _userService: UserService,
    private _router: Router, private _activeRoute: ActivatedRoute, private _auth: AuthenticationService) { }

  ngOnInit(): void {
    this.userForm = this.formBuilder.group({
      username: ["", [Validators.required]],
      email: ["", [Validators.required, Validators.email]],
      role: ["", [Validators.required]],
      gender: ["male", [Validators.required]],
      password: ["", [Validators.required, Validators.minLength(5)]],
      confirmPassword: ["", Validators.required],
      dateOfBirth: ["", [Validators.required]],
      startDate: ["", [Validators.required]],
      position: ["", [Validators.required]],
      education: ["", [Validators.required]],
      phoneNumber: ["", [Validators.required]],
      emergencyContact: ["", []],
      address: ["", [Validators.required]],
      relationship: ["", [Validators.required]],
    },
      {
        validator: MustMatch("password", "confirmPassword"),
      }
    );
    this.checkAction();
  }

  checkAction() {
    this._activeRoute.params.subscribe(params => {
      this.userId = params.userId;
      this.routeRole = params.role;
      if (params.role == Role.Student.toLowerCase()) this.isStudentCreate = true;
      if (this.userId) {
        this.getUserDetail();
      }
    });
  }

  getUserDetail() {
    this.subscription = this._userService.getUserDetail(this.userId)
      .subscribe((res) => {
        let user = res.user;
        this.currentRole = user.role;
        this.changeRole(user.role);
        this.uploadImg = user.image;
        this.studentList = user.students ? user.students : [];
        this.userForm = this.formBuilder.group({
          username: [user.username, [Validators.required]],
          email: [user.email, [Validators.required, Validators.email]],
          role: [user.role, [Validators.required]],
          gender: [user.gender, [Validators.required]],
          password: ["", [Validators.minLength(5)]],
          confirmPassword: [""],
          dateOfBirth: [user.dateOfBirth, [Validators.required]],
          startDate: [user.startDate, [Validators.required]],
          position: [user.position, [Validators.required]],
          education: [user.education, [Validators.required]],
          phoneNumber: [user.phoneNumber, [Validators.required]],
          emergencyContact: [user.emergencyContact, [Validators.required]],
          address: [user.address, [Validators.required]],
          relationship: [user.relationship, [Validators.required]],
        },
          {
            validator: MustMatch("password", "confirmPassword"),
          }
        );
      });

  }

  onSubmit() {
    console.log(this.getStudentIdString());
    if (this.currentRole == Role.Parent) {
      ['startDate', 'position', 'education', 'emergencyContact'].map(d => this.userForm.controls[d].setErrors(null))
    } else {
      this.userForm.controls['relationship'].setErrors(null)
    }
    console.log(this.userForm.value)
    if (this.userId && !this.isChangePassword)
      this.userForm.controls['password'].setErrors(null);
    this.submitted = true
    if (this.userForm.invalid) return;
    else {
      if (this.userId) {
        this.onUpdate();
      } else {
        this.onCreate();
      }
    }
  }

  getFinalData() {
    let user = new User();
    user.username = this.userForm.value.username;
    user.email = this.userForm.value.email;
    user.role = this.currentRole;
    user.gender = this.userForm.value.gender;
    user.dateOfBirth = this.userForm.value.dateOfBirth;
    user.startDate = this.userForm.value.startDate;
    user.position = this.userForm.value.position;
    user.education = this.userForm.value.education;
    user.phoneNumber = this.userForm.value.phoneNumber;
    user.emergencyContact = this.userForm.value.emergencyContact;
    user.address = this.userForm.value.address;
    if (this.userId && this.isChangePassword) user.password = this.userForm.value.password;
    else user.password = this.userForm.value.password;
    user.relationship = this.userForm.value.relationship;
    if (this.currentRole == Role.Parent) {
      user.students = this.getStudentIdString();
    }
    let getImg = document.getElementById("blobUrl");
    let img;
    img =
      getImg != undefined
        ? document.getElementById("blobUrl").getAttribute("src")
        : (img = this.uploadCrop);
    if (img != undefined) {
      let ulFile = this.dataURItoBlob(img);
      console.log(ulFile)
      user.file = ulFile;
    }
    return user;
  }

  getStudentIdString() {
    let temp = this.studentList.map((stud) => {
      return stud._id;
    });
    console.log(temp);
    return temp.join(',');
  }
  onCreate() {
    let finalData = this.getFinalData();
    console.log(finalData)
    this.subscription = this._userService.createUser(finalData)
      .subscribe(
        data => {
          this.goBack();

        },
        error => {
          console.log(error);
          // this.loading = false;
        });
  }

  onUpdate() {
    let finalData = this.getFinalData();
    finalData._id = this.userId;
    this.subscription = this._userService.updateUser(finalData)
      .subscribe(
        () => {
          this.goBack();
          //this.userForm.reset()
        },
        error => {
          console.log(error);
          // this.loading = false;
        });
  }

  displayStudentForm(val: boolean) {
    this.isStudentCreate = val;
  }

  addStudent(std = null) {
    this.userForm.get('role').disable();
    this.studentList.push(std);
    this.isStudentCreate = false;
  }

  get f() { return this.userForm.controls }

  changeRole(role) {
    this.currentRole = role;
    if (this.currentRole == Role.Admin)
      this.position = [AdminPosition.Principal, AdminPosition.VicePrincipal, AdminPosition.HOD, AdminPosition.Coordinator, AdminPosition.BOD, AdminPosition.Secretary];
    else if (this.currentRole == Role.Teacher)
      this.position = [TeacherPosition.HomeroomTeacher,TeacherPosition.AssitantTeacher,TeacherPosition.SubjectTeacher];
      else if (this.currentRole == Role.StudentAffair)
      this.position = [StaffPosition.Receptionist,StaffPosition.OfficeStaff,StaffPosition.Librarain,StaffPosition.Accountant,];

  }



  changePosition(e) {
    this.pos.setValue(e.target.value, {
      onlySelf: true
    })
  }

  get pos() {
    return this.userForm.get('position');
  }



  changeRelationship(e) {
    this.rs.setValue(e.target.value, {
      onlySelf: true
    })
  }

  get rs() {
    return this.userForm.get('relationship');
  }

  // isAdminOrTeacherOrStdAffairRole(){
  //   return [Role.Admin, Role.Teacher, Role.StudentAffair].includes(this.selectedRole)
  // }


  uploadCropImg($event: any) {
    var image: any = new Image();
    this.blankCrop = true;
    $(".frame-upload").css("display", "block");
    this.imgDemoSlider = true;
    $("#upload-demo img:first").remove();
    this.input = $event.target.files[0];
    // if (this.input.size <= 477732 && this.input) {
    if (this.input && this.uploadCrop) {
      this.uploadCrop.destroy();
    }
    var reader: FileReader = new FileReader();
    this.uploadCrop = new Croppie(document.getElementById("upload-demo"), {
      viewport: {
        width: 120,
        height: 120,
        type: "circle",
      },
      boundary: {
        width: 120,
        height: 120,
      },
      enableExif: true,
    });

    var $uploadCrop = this.uploadCrop;
    reader.onload = function (e: any) {
      $(".upload-demo").addClass("ready");
      $uploadCrop
        .bind({
          url: e.target.result,
        })
        .then(function (e: any) { });
    };
    reader.readAsDataURL($event.target.files[0]);
    // } else {
    //   console.log('file size is too large');
    //   // this.toastr.error('file size is too large');
    //   this.validProfile = false;
    //   this.imgDemoSlider = false;
    //   $('.frame-upload').css('display', 'none');
    // }
  }

  cropResult() {
    this.validProfile = true;
    this.isCrop = true;
    let self = this;


    this.imgDemoSlider = false;
    setTimeout(function () {
      $(".circular-profile img:last-child").attr("id", "blobUrl");
      $(".frame-upload").css("display", "none");
      this.blankCrop = false;
    }, 200);

    var cropper = this.uploadCrop;
    var BlobUrl = this.dataURItoBlob;
    this.uploadCrop
      .result({
        circle: false,
        type: "canvas",
        size: {
          width: 120,
          height: 120,
        },
        quality: 1,
      })
      .then(function (resp: any) {
        if (resp) {
          setTimeout(function () {
            $(".circular-profile i").remove();
            $(".circular-profile img").remove();

            $(".circular-profile").append(
              '<img src="' + resp + '" width="100%" />'
            );
          }, 100);
        }
      });
  }

  dataURItoBlob(dataURI: any) {
    var byteString = atob(dataURI.split(',')[1]);
    var mimeString = dataURI
      .split(',')[0]
      .split(':')[1]
      .split(';')[0];
    var ab = new ArrayBuffer(byteString.length);
    var ia = new Uint8Array(ab);
    for (var i = 0; i < byteString.length; i++) {
      ia[i] = byteString.charCodeAt(i);
    }
    return new Blob([ab], { type: mimeString });
  }

  backToUpload() {
    // this.hideMenu = true;
    this.validProfile = false;
    this.imgDemoSlider = false;
    $(".frame-upload").css("display", "none");
  }



  goBack() {
    let userRole = this._auth.currentUserValue.role.toLowerCase();
    console.log(this.currentRole)
    if (this.currentRole)
      this._router.navigateByUrl(userRole + "/users/" + this.currentRole.replace(/\s/g, '').toLowerCase() + "-management/" + this.currentRole);
    else this._router.navigateByUrl(userRole + "/users/" + this.routeRole.replace(/\s/g, '').toLowerCase() + "-management/" + this.routeRole);
  }

  showForget() {
    this.isChangePassword = true;
  }

  ngOnDestroy() {
    if(this.subscription)
    this.subscription.unsubscribe();
  }
}

function MustMatch(controlName: string, matchingControlName: string) {
  return (formGroup: FormGroup) => {
    const control = formGroup.controls[controlName];
    const matchingControl = formGroup.controls[matchingControlName];

    if (matchingControl.errors && !matchingControl.errors.mustMatch) {
      // return if another validator has already found an error on the matchingControl
      return;
    }

    // set error on matchingControl if validation fails
    if (control.value !== matchingControl.value) {
      matchingControl.setErrors({ mustMatch: true });
    } else {
      matchingControl.setErrors(null);
    }
  };

}
